/* =========
layout /navBar
========= */
.header {
  background: rgb(105, 72, 72);
  padding: 2em 0;
  text-align: center;
  opacity: 0.8;
  width:100%;
  position: fixed;
  right: 0;
  left: 0;
  z-index: 999;
}
.container {
  width: 90%;
  /* max-width: 1000px; */
  margin: 0 auto;
  background: beige;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.navContainer {
  display: flex;
  justify-content: center;
}
nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
nav ul li{
  padding: 0.45em 0;
  letter-spacing: 1px;
}

nav ul li a {
  text-decoration: none;
  color: #707070;
  font-weight: 700;
  
}

nav ul li a:hover,
nav ul li a:focus {
  color: #471616;
}
.navHeading img {
  background: beige;
  padding: 15px 15px 0 0;
}

@media (min-width: 675px) {
  .container {
    flex-direction: row;
    background: beige;
    padding: 0 25px;
    border-radius: 7px;
  }
  nav .listContainer {
    display: flex;
    flex-direction: row;
  }
  .listItems {
    padding: 10px;
  }
  .listItems a{
    color:rgb(146, 39, 39);
    text-decoration: none;
  }
  .listItems a:hover, .listItems a:focus{
    color:rgb(90, 25, 25);
  }
  .listItems a:active {
    text-decoration: underline;
   }
  
}

/* ==================
Typography
============== */
.navHeading {
  font-family: "Kavivanar", cursive;
  font-size: 2.5em;
  margin: 15px 0 0 0;
}
.titleBlurb {
  font-family: "Dancing Script", cursive;
  font-size: 24px;
  margin: 0 0 15px 0;
}
